import React from "react";
import {connect} from "react-redux";
import {TRACK_GIFTS_VIEWS} from "../../components/view-by/ViewBy";
import InfiniteScroll from "react-infinite-scroll-component";
import {setTrackGiftsProduct, setTrackGiftsPaginationInfo} from "../../TrackGiftsAction";
import {GET_TRACK_GIFTS} from "api/queries"
import {TRACK_GIFTS_PRODUCTS_PER_REQUEST} from "../../../../utils/constants";
import {graphQL} from "../../../../api/axios";
import GiftCard from "../../components/gift-card/GiftCard";
import "./TrackGiftContent.scss";

// Component for No registry Gifts Found
const NoRecordFound = () => {
  return (
    <h2>No Gifts Found...</h2>
  )
}

class TrackGiftsContent extends React.Component {

  state = {
    currentView: null
  }

  getTrackGiftsProduct = (clearProducts = false) => {
    let reqData = {
      query: GET_TRACK_GIFTS,
      operationName: "registryGifts",
      variables: {
        registryId: this.props.stateRegistryInfo?.id,
        giftGivers: this.props.stateTrackGiftsView?.id === TRACK_GIFTS_VIEWS[1].id,  // [1] index is Gift Giver
        after: clearProducts ? null : this.props.stateTrackGiftsPagination.endCursor,
        first: TRACK_GIFTS_PRODUCTS_PER_REQUEST
      }
    }

    graphQL(reqData).then(res => {
      if (res.data.data.registryGifts && res.data.data.registryGifts.gifts && res.data.data.registryGifts.gifts.edges.length > 0) {
        let registryGifts = res.data.data.registryGifts.gifts
        let products = registryGifts.edges.map(item => ({...item.node}))
        let pagination = null
        if (registryGifts.pageInfo) {
          pagination = {
            hasNextPage: registryGifts.pageInfo.hasNextPage,
            endCursor: registryGifts.pageInfo.endCursor,
          }
        }
        if (pagination) {
          this.props.dispatch(setTrackGiftsPaginationInfo({...pagination}))
        }
        if (products.length > 0) {
          if (clearProducts) {
            this.props.dispatch(setTrackGiftsProduct([...products]))
          } else {
            this.props.dispatch(setTrackGiftsProduct([...this.props.stateTrackGiftsProducts, ...products]))
          }
        }
      } else {
        this.props.dispatch(setTrackGiftsPaginationInfo({
          hasNextPage: false,
          endCursor: null
        }))
      }
    }).catch(err => {
      this.props.dispatch(setTrackGiftsPaginationInfo({
        hasNextPage: false,
        endCursor: null
      }))
      console.error(err.response)
    })
  }

  checkProductAvailability = product => {
    // Checking if product has any guestUser or not (Availability)
    if (!product.isGroupGift) {
      return !(product.isReserved || product.isPurchased || (product.isCashAcceptedOnly && product.isAmountReceived))
    } else {
      if (product.groupGift) {
        return product.groupGift.guestUser.length <= 0
      } else {
        return true
      }
    }
  }

  getProductsBasedOnView = (view, stateProducts) => {
    let products = []
    let availableProducts = []
    let guestUsersEmails = []

    if (view?.id === TRACK_GIFTS_VIEWS[0].id) { // [0] index is Gifts
      //  Gifts View
      for (const product of stateProducts) {
        if (this.checkProductAvailability(product)) {
          availableProducts.push({...product})
        } else {
          products.push({...product})
        }
      }

      // Show Available products in Gifts View only if Available Filter is applied
      // if (availableProducts.length > 0) {
      //   products.push(...availableProducts)
      // }
    } else {
      //  Gift Giver View
      for (const product of stateProducts) {
        if (this.checkProductAvailability(product)) {
          availableProducts.push({...product})
        } else {
          // Grouping Product
          if (product.isGroupGift) {
            // Looping through each groupGift User
            for (const groupUser of product.groupGift.guestUser) {
              // console.log(groupUser)
              if (guestUsersEmails.indexOf(groupUser.guestEmail) !== -1) {
                // User already have product
                products = products.map(groupProduct => {
                  if (groupProduct.user.guestEmail === groupUser.guestEmail) {
                    groupProduct.products.push(product)
                  }
                  return groupProduct
                })
              } else {
                // User doesn't have product
                products.push({
                  user: {...groupUser},
                  products: [{...product}]
                })
                guestUsersEmails.push(groupUser.guestEmail)
              }
            }
          } else {
            //  Not a group Gift
            if (guestUsersEmails.indexOf(product.guestUser.guestEmail) !== -1) {
              // User already have product
              products = products.map(groupProduct => {
                if (groupProduct.user.guestEmail === product.guestUser.guestEmail) {
                  groupProduct.products.push(product)
                }
                return groupProduct
              })
            } else {
              // User doesn't have product
              products.push({
                user: {...product.guestUser},
                products: [{...product}]
              })
              guestUsersEmails.push(product.guestUser.guestEmail)
            }
          }


        }
      }

      if (availableProducts.length > 0) {
        products.push({
          availableProducts: true,
          products: [...availableProducts]
        })
      }
    }

    return products
  }

  render() {

    if (this.props.stateTrackGiftsProducts.length <= 0 && this.state.currentView && this.props.stateTrackGiftsPagination.hasNextPage) {
      this.getTrackGiftsProduct(true)
    }

    // Getting new products when new views are switched
    if (this.state.currentView?.id !== this.props.stateTrackGiftsView?.id) {
      this.setState({
        currentView: {...this.props.stateTrackGiftsView}
      })
      this.getTrackGiftsProduct(true)
    }

    const products = this.getProductsBasedOnView(this.props.stateTrackGiftsView, this.props.stateTrackGiftsProducts)

    if (this.props.stateTrackGiftsProducts.length <= 0 && !this.props.stateTrackGiftsPagination.hasNextPage) {
      return <NoRecordFound/>
    } else if (this.props.stateTrackGiftsView?.id === TRACK_GIFTS_VIEWS[0].id) {

      // For Gifts

      return (
        <div>
          <div className="row mb-2">
            <div className="col-6">
              <p className="text-sm font-medium">Gift details</p>
            </div>
            <div className="col-6">
              <p className="text-sm font-medium">Gifted by</p>
            </div>
          </div>
          <div className="row">
            <div className="col">
              <InfiniteScroll
                next={this.getTrackGiftsProduct}
                hasMore={this.props.stateTrackGiftsPagination.hasNextPage}
                loader={<h4>Loading...</h4>}
                dataLength={this.props.stateTrackGiftsProducts.length}
              >
                {products.map(product => {
                  // return <p className="my-5">{product.title}</p>
                  return <GiftCard data={product}/>
                })}
              </InfiniteScroll>
            </div>
          </div>
        </div>
      )
    } else {

      // For Gift Giver

      return (
        <div>
          <InfiniteScroll
            next={this.getTrackGiftsProduct}
            hasMore={this.props.stateTrackGiftsPagination.hasNextPage}
            loader={<h4>Loading...</h4>}
            dataLength={this.props.stateTrackGiftsProducts.length}
          >
            {products.map(groupedProduct => {
                return (
                  <div className="mb-4 track-gifts-gift-giver-view">
                    <div className="mb-2">
                    <span
                      className="text-body font-medium">{groupedProduct.availableProducts ? "Available" : `${groupedProduct.user.firstName} ${groupedProduct.user.lastName}`}
                    </span>
                      {!groupedProduct.availableProducts ? <span className="ml-1 text-body">{groupedProduct.user.guestEmail}</span> : null }
                      <span
                        className="ml-1 text-body font-medium gift-givers-user-gift-count px-2">{groupedProduct.products.length} gifts</span>
                    </div>
                    <div>
                      {groupedProduct.products.map(product => (
                        <GiftCard data={product}/>
                      ))}
                    </div>
                  </div>
                )
            })}
          </InfiniteScroll>
        </div>
      )
    }
  }
}

const mapStateToProps = state => ({
  stateTrackGiftsView: state.trackGifts.activeView,
  stateTrackGiftsProducts: state.trackGifts.products,
  stateTrackGiftsPagination: state.trackGifts.paginationInfo,
  stateRegistryInfo: state.registry.info
})

export default connect(
  mapStateToProps
)(TrackGiftsContent);