const GET_USERS = gql`
query getUsers {
    users {
        id,
        email
    }
}
`;

const GET_CATEGORIES = `
query getCategories($registryId: Int!){
  registryCategories(registryId:$registryId){
    id,
    isExcluded,
    itemsCount,
    addedItemsCount,
    totalItemsCount,
    subCategoriesIsExcluded,
    category{
      id,
      categoryName,
      description,
      categoryType {
        id,
        name
      }
      isParent,
    },
    registrySubCategories{
      id,
      isExcluded,
      itemsCount,
      addedItemsCount,
      totalItemsCount,
      subCategoriesIsExcluded,
      category{
        id,
        categoryName,
        description,
        categoryType {
          id,
          name
        }
        isParent,
      },
      registrySubProductTypes{
        id,
        isExcluded,
        itemsCount,
        addedItemsCount,
        totalItemsCount,
        category{
          id,
          categoryName,
          description,
          categoryType {
            id,
            name
          }
          isParent,
        },
      }
    }
  }
 }
`;

const GET_PRODUCTS = `
query registryItems(
  $registryId:Int!
  $after: String, 
  $first: Int, 
  $isReserved: Boolean,
  $isPurchased: Boolean,
  $isAvailable: Boolean,
  $sortBy: String,
  $categories:[Int],
  $price_Lte: Float,
  $price_Gte: Float,
  $store: [String],
  ){
    registryItems(
      registryId:$registryId
      isReserved: $isReserved,
      isPurchased: $isPurchased,
      isAvailable: $isAvailable,
      sortBy: $sortBy,
      categories: $categories,
      price_Lte: $price_Lte,
      price_Gte: $price_Gte,
      store: $store,
      ){
      registryItems(after: $after, first: $first) {
        edges{
          node{
            id,
            registryitemImage{
              image
            }
            isGroupGift,
            isReserved,
            quantity,
            mostWanted,
            featuredOrder,
            orderNo,
            giftMessage,
            isReserved,
            isAmountReceived,
            isPurchased,
            title,
            registryCategories {
              id,
              category{
                categoryName,
                categoryType {
                  id,
                  name
                }
              }
            }
            note,
            item{
              store{
                id,
                storeName
              }
              id,
              shopifyItemId,
              sku,
              price,
              imageUrl,
            }
          }
        }
        pageInfo{
          endCursor,
          hasNextPage
        }
      }
      registryItemsAnalysisCount{
        totalAvailable,
        totalReserved,
        totalPurchased,
        maStoreItems,
        otherStoreItems,
        under50,
        moreThan100,
        between50100,
        totalFundsReceived
      }
      categoryData
    }
  }
`

const GET_RECOMMENDATIONS = `
query recommendation($productCount: Int!, $productType: String!){
  products(first:$productCount,
    query:$productType){
      edges{
        node{
          id,
          handle,
          title,
          description,
          vendor,
          onlineStoreUrl,
          variants (first:1){
            edges {
              node {
                price,
                sku,
                image{
                  originalSrc
                }
              }
            }
          }
        }
      }
    }
  }
`;

const ADD_REGISTRY_ITEM = `
mutation addRegistryItems(
    $registryId:Int!,
    $title:String!,
    $category:[Int]!,
    $tags:[Int]!,
    $storeItemUrl:String!,
    $sku:String!,
    $price:Float!,
    $quantity:Int!,
    $shopifyItemId:String,
    $imageUrl: String){
  addRegistryItems(registryId:$registryId,
  registryItems:[{
    storeName:"monicaandandy",
    items: [{
      title:$title,
      registryCategories:$category,
      tags:$tags,
      storeItemUrl:$storeItemUrl,
      sku:$sku,
      price:$price,
      quantity:$quantity,
      shopifyItemId: $shopifyItemId,
      imageUrl: $imageUrl
    },]
  }])
  {
    message
  }
}
`;

const UPDATE_REGISTRY_ITEM = `
mutation updateRegistryItems($registryItemId: Int!, $quantity: Int!){
  updateRegistryItems(registryItemId:$registryItemId, registryItemData:{
    quantity:$quantity,
    isReserved:false,
  }){
    message
  }
}
`;

const REMOVE_REGISTRY_ITEM = `
mutation deleteRegistryItems($registryItemId: Int!){
  deleteRegistryItems(registryItemId:$registryItemId){
    message
  }
}
`;

