const GET_TRACK_GIFTS = `
query registryGifts($registryId: Int!, $fundsReceived: Boolean, $giftGivers: Boolean!, $after: String, $first: Int) {
  registryGifts(registryId: $registryId, fundsReceived: $fundsReceived, giftGivers: $giftGivers) {
    gifts(after: $after, first: $first) {
      edges {
        node {
          id
          isGroupGift
          featuredOrder
          isReserved
          isPurchased
          isCashAcceptedOnly
          isAmountReceived
          giftMessage
          item {
            storeItemUrl
            price
            imageUrl
            isCashFund
          }
          title
          guestUser {
            guestEmail
            firstName
            lastName
            id
            contributionAmount
          }
          groupGift {
            id
            amountReceived
            guestUser {
              guestEmail
              firstName
              lastName
              id
              contributionAmount
            }
          }
        }
      }
      pageInfo {
        endCursor
        hasNextPage
      }
    }
  }
}
`

