import React from 'react';
import { connect } from "react-redux";
import GuestSortFilter from "../guest-sort-filter/GuestSortFilter";
import './GuestSortSection.scss';
import { clearGuestViewFilter, setChildGuestViewFilter, setGuestViewProducts, clearViewGuestPaginationInfo } from "../../GuestViewAction";
import FilterItemChip from "modules/my-registry/components/filter/filter-item-chip/FilterItemChip"

class GuestSortSection extends React.Component {

  // Filter chips close handler
  deleteChipHandler = async (isParent, childId) => {
    if (isParent) {
      await this.props.dispatch(clearGuestViewFilter())
    } else {
      const newChildArray = this.props.stateFilterApplied.childFilter.filter(element => {
        return element !== childId
      })
      await this.props.dispatch(setChildGuestViewFilter(newChildArray))
    }
    await this.props.dispatch(clearViewGuestPaginationInfo())
    await this.props.dispatch(setGuestViewProducts([]))
    await this.props.setFilterhandler()
  }

  //Clear filter state
  clearFilterHandler = async (ev) => {
    ev.preventDefault()
    await this.props.dispatch(clearGuestViewFilter())
    await this.props.dispatch(clearViewGuestPaginationInfo())
		await this.props.dispatch(setGuestViewProducts([]))
    await this.props.setFilterhandler()
  }

  render() {
    let filterChips = {}
    if (this.props.stateFilterApplied) {
      let allChild = []
      for (let filterCat of this.props.stateFilterCategory) {
        for (let childCat of filterCat.child) {
          allChild.push(childCat)
        }
      }

      if (this.props.stateFilterApplied.childFilter) {
        filterChips.childFilter = this.props.stateFilterApplied.childFilter.map(item => {
          return allChild.find(element => {
            return element.id === item
          })
        })
      }
    }

    return (
      <>
        <div className="row">
          <div className="col">
            <h4 className="font-weight-bold mr-2 filter-section-total-items">{this.props.productCount} {this.props.productCount === 1 ? "Gift" : "Gifts"}</h4>
          </div>
          <div className="col-5 filter-sort-section">
            <div className="d-flex justify-content-end">
              <div className="text-sm sort-by-text mr-2 align-self-center">Sort by</div>
              <GuestSortFilter />
            </div>

          </div>
        </div>
        <div className="row">
          <div className="col-7">
            <div className="filtered-items">
              {filterChips.childFilter ?
                filterChips.childFilter.map(item =>
                  <FilterItemChip
                    text={item.display}
                    className="mr-2"
                    key={filterChips.id + "-" + item.id}
                    deleteHandler={() => this.deleteChipHandler(false, item.id)} />)
                : null}
              {this.props.stateFilterApplied.childFilter.length > 0 ?
                <a className="font-medium text-sm filter-clear-link" href="#" tabindex
                  onClick={(ev) => this.clearFilterHandler(ev)}>Clear
                all</a>
                : null}
            </div>
          </div>
        </div>
      </>
    )
  }
}


const mapStateToProps = state => {
  return {
    stateViewRegistryProducts: state.guestView.product,
    stateActiveSortFilter: state.guestView.activeGuestSortFilter,
    stateGuestViewRegistry: state.guestView.guestViewRegistry,
    stateRegistryInfo: state.guestView.info,
    stateFilterApplied: state.guestView.guestViewFilterApplied,
    stateFilterCategory: state.guestView.guestFilterCategory
  }
}

export default connect(
  mapStateToProps
)(GuestSortSection);
