import React from "react";
import "./GuestView.scss";
import { connect } from "react-redux";
import { updateGuestViewRegistry, getGuestCategories } from "../../utils/commonFunction";
import Button from "../commons/components/button-component/ButtonComponent";
import GuestViewHeader from "./containers/guest-view-header/GuestViewHeader";
import GuestSortSection from "./containers/guest-sort/GuestSortSection";
import GuestViewCard from "./components/GuestViewCard";
import InfiniteScroll from "react-infinite-scroll-component";
import { VIEW_GIFT_PRODUCTS_PER_REQUEST } from "utils/constants";
import { setGuestViewProducts, setViewGuestPaginationInfo, updateGuestViewInfo, setGuestViewFilterCategory } from './GuestViewAction';
import { GET_GUEST_REGISTRY_PRODUCT } from 'api/queries';
import { MEDIA_URL_PREFIX } from "utils/constants";
import { graphQL } from 'api/axios';
import GuestFilter from './containers/guest-filter/GuestFilter';
import { FILTERS } from "utils/constants";


class GuestView extends React.Component {

  state = {
    isFirstTime: true
  }

  BABY_GIFTS_URL = "https://monicaandandy.com/collections/baby-gifts"

  // Constant variables for filters
  AVAILABLE = "available"
  RESERVED = "reserved"
  PURCHASED = "purchased"
  PRICE_1 = "price-1"
  PRICE_2 = "price-2"
  PRICE_3 = "price-3"
  MONICA_AND_ANDY = "monicaandandy"
  ELSEWHERE = "elsewhere"

  // Updating product count in filter categories
  updateFilterList = (registryItemsAnalysisCount, categoryData) => {

    let newFilterList = [
      {
        id: "giftStatus",
        display: "Gift Status",
        child: [
          {
            id: this.AVAILABLE,
            display: FILTERS.AVAILABLE_DISPLAY,
            count: registryItemsAnalysisCount.totalAvailable
          },
          {
            id: this.RESERVED,
            display: FILTERS.RESERVED_DISPLAY,
            count: registryItemsAnalysisCount.totalReserved
          },
          {
            id: this.PURCHASED,
            display: FILTERS.PURCHASED_DISPLAY,
            count: registryItemsAnalysisCount.totalPurchased
          }
        ]
      },
      {
        id: "categories",
        display: FILTERS.CATEGORIES_DISPLAY,
        // limit: this.getLimitOfFliter("categories") ? this.getLimitOfFliter("categories") : 6,
        child: [...this.props.stateCategoriesInfo].filter(category => {
          let id = category.category.id
          let count = categoryData[id]
          if (count > 0) {
            return true
          }
        }).map(category => {
          let newCategory = { ...category }
          let id = category.category.id
          newCategory.id = category.category.id
          newCategory['display'] = newCategory.categoryName
          newCategory['count'] = categoryData[id]
          return newCategory
        })
      },
      {
        id: "price",
        display: FILTERS.PRICE_DISPLAY,
        child: [
          {
            id: this.PRICE_1,
            display: FILTERS.PRICE_DISPLAY_1,
            count: registryItemsAnalysisCount.under50
          },
          {
            id: this.PRICE_2,
            display: FILTERS.PRICE_DISPLAY_2,
            count: registryItemsAnalysisCount.between50100
          },
          {
            id: this.PRICE_3,
            display: FILTERS.PRICE_DISPLAY_3,
            count: registryItemsAnalysisCount.moreThan100
          }
        ]
      },
      {
        id: "store",
        display: FILTERS.STORE_DISPLAY,
        child: [
          {
            id: this.MONICA_AND_ANDY,
            display: FILTERS.MONICA_AND_ANDY_DISPLAY,
            count: registryItemsAnalysisCount.maStoreItems
          },
          {
            id: this.ELSEWHERE,
            display: FILTERS.ELSEWHERE_DISPLAY,
            count: registryItemsAnalysisCount.otherStoreItems
          },
        ]
      },

    ]
    this.props.dispatch(setGuestViewFilterCategory(newFilterList))
    return newFilterList
  }

  getFiltersApplied = (stateFilterApplied, activeSortFilterState) => {
    const childFilterArray = [...stateFilterApplied]
    let filterApplied = {}
    // For Gift Status
    if (childFilterArray.includes(this.AVAILABLE)) {
      filterApplied.isAvailable = true
    }
    if (childFilterArray.includes(this.RESERVED)) {
      filterApplied.isReserved = true
    }
    if (childFilterArray.includes(this.PURCHASED)) {
      filterApplied.isPurchased = true
    }

    // For Sort
    if (activeSortFilterState.sortId === 2) {
      filterApplied.sortBy = 'item__price'
    } else if (activeSortFilterState.sortId === 3) {
      filterApplied.sortBy = '-item__price'
    }

    // For Store
    let store = []
    if (childFilterArray.includes(this.MONICA_AND_ANDY)) {
      store.push(this.MONICA_AND_ANDY)
    }
    if (childFilterArray.includes(this.ELSEWHERE)) {
      store.push(this.ELSEWHERE)
    }
    if (store.length) {
      filterApplied.store = store
    }

    let lessThanEqual = 0
    let greaterThanEqual = 0
    let hasPriceFilter = false
    // For Price
    if (childFilterArray.includes(this.PRICE_1) &&
      childFilterArray.includes(this.PRICE_2) &&
      childFilterArray.includes(this.PRICE_3)) {
      greaterThanEqual = 0
      hasPriceFilter = true
    }
    else if (childFilterArray.includes(this.PRICE_1) &&
      childFilterArray.includes(this.PRICE_2)) {
      lessThanEqual = 100
      hasPriceFilter = true
    }
    else if (childFilterArray.includes(this.PRICE_1) &&
      childFilterArray.includes(this.PRICE_3)) {
      lessThanEqual = 49
      greaterThanEqual = 100
      hasPriceFilter = true
    }
    else if (childFilterArray.includes(this.PRICE_2) &&
      childFilterArray.includes(this.PRICE_3)) {
      greaterThanEqual = 50
      hasPriceFilter = true
    }
    else if (childFilterArray.includes(this.PRICE_1)) {
      lessThanEqual = 49
      hasPriceFilter = true
    }
    else if (childFilterArray.includes(this.PRICE_2)) {
      greaterThanEqual = 50
      lessThanEqual = 100
      hasPriceFilter = true
    }
    else if (childFilterArray.includes(this.PRICE_3)) {
      greaterThanEqual = 100
      hasPriceFilter = true
    }
    if (hasPriceFilter) {
      filterApplied.price_Lte = lessThanEqual
      filterApplied.price_Gte = greaterThanEqual
    }

    let categories = []
    // For categories
    for (let cat of [...this.props.stateCategoriesInfo]) {
      if (childFilterArray.includes(cat.category.id)) {
        categories.push(cat.category.id)
      }
    }
    if (categories.length) {
      filterApplied.categories = categories
    }

    return filterApplied
  }

  updateGuestViewRegistryProduct = (urlSlug) => {
    let filtersApplied = this.getFiltersApplied(this.props.stateFilterApplied, this.props.activeSortFilterState)
    let reqData = {
      query: GET_GUEST_REGISTRY_PRODUCT,
      variables: {
        urlSlug: urlSlug,
        after: this.props.stateGuestViewPagination.endCursor,
        first: VIEW_GIFT_PRODUCTS_PER_REQUEST,
        ...filtersApplied
      }
    }

    graphQL(reqData).then(res => {
      if (res.data.data.registryAsGuest) {

        // Storing PagenationInfo
        let pagenationInfo = { ...res.data.data.registryAsGuest.registryItems.pageInfo }
        this.props.dispatch(setViewGuestPaginationInfo(pagenationInfo))

        // Storing Product of GuestView
        let responseData = [...res.data.data.registryAsGuest.registryItems.edges].map(item => {
          let newItemObj = { ...item.node }
          delete newItemObj.registryitemImage;
          if (item.node.registryitemImage.length > 0) {
            newItemObj.item.imageUrl = (MEDIA_URL_PREFIX.LOCAL_MEDIA_URL ? MEDIA_URL_PREFIX.LOCAL_MEDIA_URL : "") + item.node.registryitemImage[0].image;
          }
          return newItemObj;
        })
        let finalData = responseData.filter(product => product.registryCategories.length > 0)
        this.props.dispatch(setGuestViewProducts(finalData))
        this.updateFilterList({ ...res.data.data.registryAsGuest.registryItemsAnalysisCount }, { ...res.data.data.registryAsGuest.categoryData })
      }
    }).catch(err => {
      this.props.dispatch(updateGuestViewInfo({ notFound: true }))
      if (err.response && err.response.data.errors) {
        for (const error of err.response.data.errors) {
          // TODO: Implement toast if necessary
          console.log(error.message)
        }
      }
    })
  }

  sortByHighToLow = (productList) => {
    return productList.sort((a, b) => { return b.item.price - a.item.price });
  }

  sortByLowToHigh = (productList) => {
    return productList.sort((a, b) => { return a.item.price - b.item.price });
  }

  getSortedFilteredProducts = (guestProducts, activeSortFilterState, filterApplied) => {
    let sortedProducts = [];
    if (activeSortFilterState.sortId === 1) {
      sortedProducts = [...guestProducts]
    }

    else if (activeSortFilterState.sortId === 2) {
      sortedProducts = this.sortByLowToHigh([...guestProducts])
    }
    else if (activeSortFilterState.sortId === 3) {
      sortedProducts = this.sortByHighToLow([...guestProducts])
    }

    return sortedProducts;
  }

  getdataForFirstTime = async () => {
    await getGuestCategories(this.props.match.params.registrySlug, this.props.dispatch)
    await this.props.dispatch(setGuestViewProducts([]))
    await this.updateGuestViewRegistryProduct(this.props.match.params.registrySlug)
  }

  render() {

    if (this.props.stateGuestViewRegistry.urlSlug !== this.props.match.params.registrySlug) {
      updateGuestViewRegistry(this.props.match.params.registrySlug, this.props.dispatch)
    }

    // When registry doesn't exists
    if (this.props.stateGuestViewRegistry.notFound) {
      return <h1>Registry not found</h1>;
    }

    if (this.props.stateGuestViewRegistry.id) {
      // When registry exists and it is not public
      if (this.props.stateGuestViewRegistry.isPublic === false) {
        let registryUsers = [this.props.stateGuestViewRegistry.user.firstName]
        if (this.props.stateGuestViewRegistry.user.partnerUser) {
          registryUsers.push(this.props.stateGuestViewRegistry.user.partnerUser.firstName)
        }
        return (
          <div className="container mt-17">
            <div className="row">
              <div className="col text-center">
                <h1>{this.props.stateGuestViewRegistry.displayName}</h1>
                {/*TODO: Display partner name as well below*/}
                {registryUsers.length === 1 ? <h4 className="guest-view-curator-names mt-2"><span className="text-capitalize">{registryUsers[0]}</span> is still curating their baby registry</h4> : null}
                {registryUsers.length === 2 ? <h4 className="guest-view-curator-names mt-2"><span className="text-capitalize">{registryUsers[0]}</span> and <span className="text-capitalize">{registryUsers[1]}</span> are still curating their baby registry</h4> : null}

                <Button
                  variant="primary"
                  className="py-1 px-2 mt-3 text-body font-medium"
                  onClick={() => window.location.href = this.BABY_GIFTS_URL}
                >Shop baby Gifts</Button>
              </div>
            </div>
          </div>
        )
      } else {

        let filteredSortProducts = this.getSortedFilteredProducts(this.props.stateAddedProducts, this.props.activeSortFilterState, this.props.stateFilterApplied)

        // Fetching product for first time 
        if (this.state.isFirstTime && this.props.stateAddedProducts.length <= 0 && this.props.stateGuestViewRegistry.urlSlug) {
          this.setState({
            isFirstTime: false
          })
          this.getdataForFirstTime()
          // this.updateFilterList(this.props.stateAddedProducts)
        }

        // When registry exists and it is public
        return (
          <>
            <GuestViewHeader />
            <div className="container mt-7">
              <div className="row">
                <div className="col-2">
                  <GuestFilter setFilterhandler={() => this.updateGuestViewRegistryProduct(this.props.match.params.registrySlug)} />
                </div>
                <div className="col"></div>
                <div className="col-9">
                  <GuestSortSection
                    productCount={filteredSortProducts.length}
                    setFilterhandler={() => this.updateGuestViewRegistryProduct(this.props.match.params.registrySlug)}
                  />

                  <div className="mt-5">
                    <InfiniteScroll
                      next={() => this.updateGuestViewRegistryProduct(this.props.match.params.registrySlug)}
                      hasMore={this.props.stateGuestViewPagination.hasNextPage}
                      loader={<h4>Loading...</h4>}
                      dataLength={this.props.stateAddedProducts.length}
                      style={{ overflow: "hidden" }}
                    >

                      {filteredSortProducts.length > 0 ?
                        filteredSortProducts.map((card, index) =>
                          <GuestViewCard
                            data={card} key={"addedCard" + index}
                          />) : null}
                    </InfiniteScroll>
                  </div>
                </div>
              </div>
            </div>
          </>
        )
      }
    }


    // Default return
    return <></>
  }
}

const mapStateToProps = (state) => {
  return {
    stateGuestViewRegistry: state.guestView.info,
    stateAddedProducts: state.guestView.product,
    activeSortFilterState: state.guestView.activeGuestSortFilter,
    stateFilterApplied: state.guestView.guestViewFilterApplied.childFilter,
    stateGuestViewPagination: state.guestView.paginationInfo,
    stateCategoriesInfo: state.guestView.checkList,
  }
}

export default connect(
  mapStateToProps
)(GuestView);