import React, { useEffect, useState } from "react";
import ViewRegistryHeader from "../view-registry-header/ViewRegistryHeader";
import AddedCard from "modules/commons/components/added-card/AddedCard";
import { toggleMustHaveProduct, removeProductFromCartHandler, getProducts, updateViewRegistryPosition, getCategories } from "utils/commonFunction";
import { useSelector, useDispatch } from 'react-redux';
import SortFilterSection from '../../components/sort-filter-section/SortFilterSection';
import { setViewRegistryProducts, clearViewFilter, setViewFilterCategory } from '../../ViewRegistryActions';
import {
  sortableContainer,
  sortableElement,
} from 'react-sortable-hoc';
import arrayMove from 'array-move';
import './ViewRegistryHome.scss';
import ViewFilter from "modules/view-registry/components/view-filter/ViewFilter";
import Button from "modules/commons/components/button-component/ButtonComponent";
import { FILTERS } from "utils/constants"
import { MEDIA_URL_PREFIX } from "utils/constants";
import { graphQL } from 'api/axios';
import { GET_PRODUCTS } from 'api/queries';
import GuestViewCard from "modules/guest-view/components/GuestViewCard";

const ViewRegistryHome = () => {

  const dispatch = useDispatch()
  let stateAddedProducts = useSelector(state => state.myRegistry.addedProducts)
  let stateViewRegistryProducts = useSelector(state => state.viewRegistry.viewRegistryProducts)
  const activeSortFilterState = useSelector(state => state.viewRegistry.activeSortFilter)
  const stateFilterApplied = useSelector(state => state.viewRegistry.viewFilterApplied.childFilter)
  const stateRegistryInfo = useSelector(state => state.registry.info)
  const stateCategoriesInfo = useSelector(state => state.myRegistry.checkList)
  const stateGuestViewRegistry = useSelector(state => state.viewRegistry.guestViewRegistry)
  const [localAddedProductState, handleFilterProductChange] = useState([])

  // Constant variables for filters
  const AVAILABLE = "available"
  const RESERVED = "reserved"
  const PURCHASED = "purchased"
  const PRICE_1 = "price-1"
  const PRICE_2 = "price-2"
  const PRICE_3 = "price-3"
  const MONICA_AND_ANDY = "monicaandandy"
  const ELSEWHERE = "elsewhere"

  // Updating product count in filter categories
  const updateFilterList = (registryItemsAnalysisCount, categoryData) => {

    let newFilterList = [
      {
        id: "giftStatus",
        display: "Gift Status",
        child: [
          {
            id: AVAILABLE,
            display: FILTERS.AVAILABLE_DISPLAY,
            count: registryItemsAnalysisCount.totalAvailable
          },
          {
            id: RESERVED,
            display: FILTERS.RESERVED_DISPLAY,
            count: registryItemsAnalysisCount.totalReserved
          },
          {
            id: PURCHASED,
            display: FILTERS.PURCHASED_DISPLAY,
            count: registryItemsAnalysisCount.totalPurchased
          }
        ]
      },
      {
        id: "categories",
        display: FILTERS.CATEGORIES_DISPLAY,
        // limit: getLimitOfFliter("categories") ? getLimitOfFliter("categories") : 6,
        child: [...stateCategoriesInfo].filter(category => {
          let id = category.category.id
          let count = categoryData[id]
          if (count > 0) {
            return true
          }
        }).map(category => {
          let newCategory = { ...category }
          let id = category.category.id
          newCategory.id = category.category.id
          newCategory['display'] = newCategory.categoryName
          newCategory['count'] = categoryData[id]
          return newCategory
        })
      },
      {
        id: "price",
        display: FILTERS.PRICE_DISPLAY,
        child: [
          {
            id: PRICE_1,
            display: FILTERS.PRICE_DISPLAY_1,
            count: registryItemsAnalysisCount.under50
          },
          {
            id: PRICE_2,
            display: FILTERS.PRICE_DISPLAY_2,
            count: registryItemsAnalysisCount.between50100
          },
          {
            id: PRICE_3,
            display: FILTERS.PRICE_DISPLAY_3,
            count: registryItemsAnalysisCount.moreThan100
          }
        ]
      },
      {
        id: "store",
        display: FILTERS.STORE_DISPLAY,
        child: [
          {
            id: MONICA_AND_ANDY,
            display: FILTERS.MONICA_AND_ANDY_DISPLAY,
            count: registryItemsAnalysisCount.maStoreItems
          },
          {
            id: ELSEWHERE,
            display: FILTERS.ELSEWHERE_DISPLAY,
            count: registryItemsAnalysisCount.otherStoreItems
          },
        ]
      },

    ]
    dispatch(setViewFilterCategory(newFilterList))
    return newFilterList
  }

  const getFiltersApplied = (stateFilterApplied, activeSortFilterState) => {
    const childFilterArray = [...stateFilterApplied]
    let filterApplied = {}
    // For Gift Status
    if (childFilterArray.includes(AVAILABLE)) {
      filterApplied.isAvailable = true
    }
    if (childFilterArray.includes(RESERVED)) {
      filterApplied.isReserved = true
    }
    if (childFilterArray.includes(PURCHASED)) {
      filterApplied.isPurchased = true
    }

    // For Sort
    if (activeSortFilterState.sortId === 2) {
      filterApplied.sortBy = 'item__price'
    } else if (activeSortFilterState.sortId === 3) {
      filterApplied.sortBy = '-item__price'
    }

    // For Store
    let store = []
    if (childFilterArray.includes(MONICA_AND_ANDY)) {
      store.push(MONICA_AND_ANDY)
    }
    if (childFilterArray.includes(ELSEWHERE)) {
      store.push(ELSEWHERE)
    }
    if (store.length) {
      filterApplied.store = store
    }

    let lessThanEqual = 0
    let greaterThanEqual = 0
    let hasPriceFilter = false
    // For Price
    if (childFilterArray.includes(PRICE_1) &&
      childFilterArray.includes(PRICE_2) &&
      childFilterArray.includes(PRICE_3)) {
      greaterThanEqual = 0
      hasPriceFilter = true
    }
    else if (childFilterArray.includes(PRICE_1) &&
      childFilterArray.includes(PRICE_2)) {
      lessThanEqual = 100
      hasPriceFilter = true
    }
    else if (childFilterArray.includes(PRICE_1) &&
      childFilterArray.includes(PRICE_3)) {
      lessThanEqual = 49
      greaterThanEqual = 100
      hasPriceFilter = true
    }
    else if (childFilterArray.includes(PRICE_2) &&
      childFilterArray.includes(PRICE_3)) {
      greaterThanEqual = 50
      hasPriceFilter = true
    }
    else if (childFilterArray.includes(PRICE_1)) {
      lessThanEqual = 49
      hasPriceFilter = true
    }
    else if (childFilterArray.includes(PRICE_2)) {
      greaterThanEqual = 50
      lessThanEqual = 100
      hasPriceFilter = true
    }
    else if (childFilterArray.includes(PRICE_3)) {
      greaterThanEqual = 100
      hasPriceFilter = true
    }
    if (hasPriceFilter) {
      filterApplied.price_Lte = lessThanEqual
      filterApplied.price_Gte = greaterThanEqual
    }

    let categories = []
    // For categories
    for (let cat of [...stateCategoriesInfo]) {
      if (childFilterArray.includes(cat.category.id)) {
        categories.push(cat.category.id)
      }
    }
    if (categories.length) {
      filterApplied.categories = categories
    }

    return filterApplied
  }

  const SortableItem = sortableElement(({ card, index }) => (
    <li className="card-list-item">
      <AddedCard
        data={card} key={"addedCard" + index}
        isViewCard={true}
        toggleMustHave={() => toggleMustHaveProduct(card, dispatch)}
        draggable={true}
        disabledDrag={card.disabledDrag}
        removeProductFromCartHandler={() => removeProductFromCartHandler(card, stateRegistryInfo.id, dispatch)}
      />
    </li>
  ));

  let onSortEnd = ({ oldIndex, newIndex }) => {
    let itemsOldPosition = arrayMove([...stateViewRegistryProducts], oldIndex, oldIndex)
    let items = arrayMove([...stateViewRegistryProducts], oldIndex, newIndex)
    dispatch(setViewRegistryProducts(items))
    updateViewRegistryPosition(stateRegistryInfo.id, items, itemsOldPosition, dispatch)
  };

  const SortableContainer = sortableContainer(({ children }) => {
    return <ul className="p-0">{children}</ul>;
  });

  const getSortedProductsByFilters = async (addedProducts, filterApplied) => {
    let finalFilterProduct = []
    let filtersApplied = getFiltersApplied(stateFilterApplied, activeSortFilterState)
    let reqData = {
      query: GET_PRODUCTS,
      variables: {
        registryId: stateRegistryInfo.id,
        ...filtersApplied
      }
    }

    await graphQL(reqData).then(res => {
      if (res.data.data.registryItems) {
        // Storing Product of GuestView
        let responseData = [...res.data.data.registryItems.registryItems.edges].map(item => {
          let newItemObj = { ...item.node }
          delete newItemObj.registryitemImage;
          if (item.node.registryitemImage.length > 0) {
            newItemObj.item.imageUrl = (MEDIA_URL_PREFIX.LOCAL_MEDIA_URL ? MEDIA_URL_PREFIX.LOCAL_MEDIA_URL : "") + item.node.registryitemImage[0].image;
          }
          return newItemObj;
        })
        let finalData = responseData.filter(product => product.registryCategories.length > 0)
        updateFilterList({ ...res.data.data.registryItems.registryItemsAnalysisCount }, { ...res.data.data.registryItems.categoryData })

        finalFilterProduct = finalData
        return finalData
      }
    }).catch(err => {

      if (err.response && err.response.data.errors) {
        for (const error of err.response.data.errors) {
          // TODO: Implement toast if necessary
          console.log(error.message)
        }
      }
    })
    return finalFilterProduct
  }

  const getParentCategory = (id) => {
    return [...stateCategoriesInfo].find((parentCategory) => parentCategory.subCategories.find((subCategory) => subCategory.id === id))
  }

  const getSortedFilteredProducts = async (addedProducts, activeSortFilterState, viewRegistryProducts, filterApplied, registryInfo) => {
    let sortedProducts = [];
    let sortedData = null;
    let newSortedProducts = null

    if (filterApplied.length > 0) {
      newSortedProducts = await getSortedProductsByFilters(addedProducts, filterApplied)
    }
    else {
      newSortedProducts = [...addedProducts]
    }

    if (activeSortFilterState.sortId === 1) {
      if (filterApplied.length > 0) {
        sortedData = newSortedProducts
      }
      else if (registryInfo.isFeatured) {
        sortedData = viewRegistryProducts ? [...viewRegistryProducts] : [];
      }
      else {
        sortedData = sortByMustHave([...newSortedProducts])
        if (sortedData.length > 0) {
          let viewRegistryProductsId = sortedData.map((productData, index) => {
            return { 'id': productData.id }
          })
        }
      }
    }

    else if (activeSortFilterState.sortId === 2) {
      sortedData = sortByLowToHigh([...newSortedProducts])
    }
    else if (activeSortFilterState.sortId === 3) {
      sortedData = sortByHighToLow([...newSortedProducts])
    }

    let viewRegistryProductsId = sortedData.map((productData, index) => {
      return { 'id': productData.id }
    })

    for (let product of viewRegistryProductsId) {
      for (let addProduct of addedProducts) {
        if (product.id === addProduct.id) {
          sortedProducts.push(addProduct);
          break;
        }
      }
    }
    // handleFilterProductChange(sortedProducts)
    return sortedProducts;
  }

  const sortByMustHave = (productList) => {
    return productList.sort(function (a, b) { return b.mostWanted - a.mostWanted });
  }

  const sortByFeatured = (productList) => {
    return productList.sort(function (a, b) { return a.isFeatured - b.isFeatured });
  }

  const sortByLowToHigh = (productList) => {
    return productList.sort((a, b) => { return a.item.price - b.item.price });
  }

  const sortByHighToLow = (productList) => {
    return productList.sort((a, b) => { return b.item.price - a.item.price });
  }

  //Clear filter state
  const clearFilterHandler = (ev) => {
    ev.preventDefault()
    dispatch(clearViewFilter())
  }

  useEffect(() => {
    let sortedData = sortByFeatured([...stateAddedProducts])
    let viewRegistryProducts = sortedData.map((productData, index) => {
      return { 'id': productData.id }
    })
    dispatch(setViewRegistryProducts(viewRegistryProducts))
  }, [stateAddedProducts])

  useEffect(() => {
    let filteredSortProducts = getSortedFilteredProducts(stateAddedProducts, activeSortFilterState, stateViewRegistryProducts, stateFilterApplied, stateRegistryInfo)
    filteredSortProducts.then(product => {
      handleFilterProductChange(product)
    })
  }, [stateAddedProducts, activeSortFilterState, stateViewRegistryProducts, stateFilterApplied, stateRegistryInfo])

  // updateFilterList(stateAddedProducts)
  let filteredSortProducts = getSortedFilteredProducts(stateAddedProducts, activeSortFilterState, stateViewRegistryProducts, stateFilterApplied, stateRegistryInfo)

  return (
    <>
      <ViewRegistryHeader />
      <div className="container mt-7">
        <div className="row">
          <div className="col-2">
            <ViewFilter />
          </div>
          <div className="col"></div>
          <div className="col-9">

            {/* Top Filter Section */}
            <SortFilterSection productCount={localAddedProductState.length} />
            {/* Added Products */}
            {!stateGuestViewRegistry ?
              localAddedProductState.length > 0 ?
                <div className="mt-5">
                  <SortableContainer onSortEnd={onSortEnd} helperClass="drag-add-card" axis="y" lockAxis="y" useDragHandle>
                    {localAddedProductState.map((card, index) =>
                      <SortableItem key={`item-${index}`} index={index} card={{ ...card, disabledDrag: activeSortFilterState.sortId !== 1 }} disabled={activeSortFilterState.sortId !== 1 || stateFilterApplied.length !== 0} />
                    )}
                  </SortableContainer>
                </div> :
                <div className="d-flex flex-column empty-filter mt-6 pb-8">
                  <div className="font-body empty-filter-title text-center mx-auto mt-10">
                    <p>We couldnt find any gifts that matches your filters...</p>
                    <p>Please try clearing Filters to view gifts.</p>
                  </div>
                  <div className="mx-auto mt-3">
                    <Button
                      variant="primary"
                      className="px-3 py-1 font-medium empty-filter-title text-body"
                      onClick={(ev) => clearFilterHandler(ev)}
                    >
                      Clear Filters
                  </Button>
                  </div>
                </div> :
              // Guest View Cards
              localAddedProductState.length > 0 ?
                localAddedProductState.map((card, index) =>
                  <GuestViewCard
                    data={card} key={"addedCard" + index}
                  />) : null
            }
          </div>
        </div>
      </div>
    </>
  )
}

export default ViewRegistryHome;