import React from "react";
import "./GiftCard.scss";
import GiftCardPill from "../gift-card-pill/GiftCardPill";
import {CURRENCY} from "../../../../utils/constants";

class GiftCard extends React.Component {
  friendsDetailRef = React.createRef()

  toggleFriendsDetail = e => {
    e.preventDefault()
    if (this.friendsDetailRef.current) {
      this.friendsDetailRef.current.classList.toggle("track-gifts-show-friends-detail")
    }
  }

  render() {
    return (
      <div className="baby-registry-track-gifts-card pt-3 pb-4">
        <div className="row">

          <div className="col-1">
            {this.props.data.item.imageUrl ?
              <img src={this.props.data.item.imageUrl} className="track-gifts-card-image"/>
              :
              <div className="track-gifts-no-product-image"/>
            }
          </div>

          <div className="col-5">
            <div className="track-gifts-product-description pl-1">
              <div className="d-flex">
                <h4
                  className={["font-medium track-gifts-product-title", this.props.data.giftMessage && this.props.data.guestUser ? "mr-5" : "mr-9"].join(" ")}>
                  {this.props.data.title}
                </h4>
                {this.props.data.giftMessage && this.props.data.guestUser ?
                  <button className="ml-auto track-gifts-gift-message-container text-left">
                    <div className="track-gifts-gift-message-content p-5">
                      <h3>Gift Message</h3>
                      <p className="text-body mt-5 track-gifts-gift-message">{this.props.data.giftMessage}</p>
                      <p
                        className="mt-3 text-sm font-medium">{`${this.props.data.guestUser.firstName} ${this.props.data.guestUser.lastName ? `${this.props.data.guestUser.lastName[0]}.` : ''}`}</p>
                    </div>
                  </button>
                  : null}

              </div>

              {this.props.data.item.isCashFund ?
                <div className="mt-2">
                  {this.props.data.groupGift ?
                    <div>
                      <span className="text-sm font-medium">{`${CURRENCY}${this.props.data.groupGift.amountReceived}`}</span>
                      <span className="text-sm">{` of ${CURRENCY}${this.props.data.item.price} received`}</span>
                      <div className="track-gifts-cash-fund-progress-bar">
                        <div className="track-gifts-cash-fund-progress mw-100"
                             style={{width: `${(this.props.data.groupGift.amountReceived / this.props.data.item.price) * 100}%`}}/>
                      </div>
                    </div>
                    : null}

                </div>
                :
                <div className="mt-2">
                  {this.props.data.isPurchased ? <GiftCardPill variant="purchased"/> : null}
                  {this.props.data.isReserved ? <GiftCardPill variant="reserved"/> : null}
                  {this.props.data.isCashAcceptedOnly && this.props.data.isAmountReceived ?
                    <GiftCardPill variant="received" amount={this.props.data.item.price}/> : null}
                  {/* TODO: Add date of purchased */}
                </div>
              }


            </div>
          </div>

          <div className="col-6">
            {this.props.data.isGroupGift ?
              // Group Gift
              (this.props.data.groupGift && this.props.data.groupGift.guestUser.length > 0 ?
                <div className="track-gifts-group-users-details">
                  <p className="text-sm font-medium">{`${this.props.data.groupGift.guestUser.length} friends`}</p>
                  <a href="#" className="track-gifts-view-friends-link text-sm font-medium"
                     onClick={(e) => this.toggleFriendsDetail(e)}>View</a>
                  <div ref={this.friendsDetailRef} className="track-gifts-friends-detail pt-3 pb-1">
                    <div className="friends-detail-header d-flex pb-2 px-3">
                      <p className="text-sm font-medium">{`${this.props.data.groupGift.guestUser.length} friends`}</p>
                      <i className="icon-close close-friends-detail-icon ml-auto" onClick={(e) => this.toggleFriendsDetail(e)}/>
                    </div>
                    <div className="px-3">
                      {this.props.data.groupGift.guestUser.map(user => (
                        <div className="friend-detail d-flex py-2">
                          <div className="mr-10">
                            <p className="text-sm font-medium">{`${user.firstName} ${user.lastName}`}</p>
                            <p className="text-sm friend-user-email">{user.guestEmail}</p>
                          </div>
                          <div className="ml-auto">
                            <p className="text-sm font-medium friend-contribution-amount">{`${CURRENCY}${user.contributionAmount}`}</p>
                            {/*  TODO: Add Contribution Date */}
                          </div>
                        </div>
                      ))}
                    </div>
                  </div>
                </div>
                : null)
              :
              // Single guest User
              (this.props.data.guestUser ?
                <div className="track-gifts-user-detail">
                  <p
                    className="text-sm font-medium">{`${this.props.data.guestUser.firstName} ${this.props.data.guestUser.lastName}`}</p>
                  <p className="text-sm track-gifts-user-email">{this.props.data.guestUser.guestEmail}</p>
                </div>
                : null)
            }
          </div>
        </div>
      </div>
    );
  }
}

export default GiftCard;